  width: 100%;
}

.login-error-msg {
  text-align: center;
  margin: 3px 0px;
  color: #DE350B;
}
.modal-overlay {
  background: rgba(0, 0, 0, 0.6);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 49;
}

.model-title {
  font-size: 24px;
  float: left;
}

.model-close-button {
  float: right;
  cursor: pointer;
}

.modal-panel {
  background-color: #eee;
  position: fixed;
  overflow-y: auto;
  padding: 10px;
  z-index: 50;
  box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
}
.toast-container {
  background-color: #000000;
  color: #FFFFFF;
  position: fixed;
  overflow-y: auto;
  padding: 10px;
  z-index: 100;
  left: 50%;
  width: 300px;
  top: 20px;
  margin-left: -150px;
}


.datasource-card {
  margin-bottom: 10px;
  background-color: #FFFFFF;
  padding: 5px;
  box-shadow: rgba(50, 50, 93, 0.14902) 0px 1px 3px, rgba(0, 0, 0, 0.0196078) 0px 1px 0px;
}

.datasource-card-title {
  font-size: 20px;
}

.datasource-icon-button {
  margin: 0px 2px 0px 2px;
}

.datasource-row-name {
  font-size: 24px;
  margin-left: 5px;
}
.checkbox-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row;
          flex-flow: row;
  margin: 3px;
}

.checkbox-checkmark {
  width: 16px;
  height: 16px;
  border: 2px solid black;
  -webkit-flex: 0 1 auto;
          flex: 0 1 auto;
  border-radius: 50%;
}

.checkbox-checkmark-box {
  width: 12px;
  height: 12px;
  margin: 2px;
  border-radius: 50%;
}

.checkbox-label {
  margin-left: 3px;
  -webkit-flex: 1 1 auto;
          flex: 1 1 auto;
  width: 180px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.slicer-body {
  max-height: 180px;
  overflow-y: auto;
}

.filterViewPanel:before,
.filterViewPanel:after {
  content: "";
  display: table;
} 
.filterViewPanel:after {
  clear: both;
}

.filter-card {
  margin: 5px;
  border: 1px solid #0065FF;
}

.filter-card-title {
  padding: 3px;
}

.icon-button-group {
  float: right;
}
/** ReactTable */
.ReactTable {
  font-size: 15px !important;
}

.ReactTable .rt-table {
  background-color: #fff;
}

.ReactTable .rt-thead.-header {
  box-shadow: none !important;
  border-bottom: 2px solid black;
}

.ReactTable .rt-tbody .rt-td {
  padding: 5px 3px;
}

.ReactTable .-pagination {
  width: 400px !important;
  box-shadow: none !important;
  border-top: none !important;
  float: right;
  margin-right: 5px;
}

.ReactTable .-pagination .-previous {
  width: 60px !important;
  -webkit-flex: none !important;
          flex: none !important;
}

.ReactTable .-pagination .-center {
  width: 270px !important;
  -webkit-flex: none !important;
          flex: none !important;
}

.ReactTable .-pagination .-next {
  width: 60px !important;
  -webkit-flex: none !important;
          flex: none !important;
}

.ReactTable .-pagination .-pageInfo {
  margin: 3px 0px 3px 0px !important;
}

.ReactTable .-pagination .-pageSizeOptions {
  margin: 3px 0px 3px 0px !important;
}

.ReactTable .-pagination .-btn {
  border: 0;
  outline: none;
  cursor: pointer;
  color: #000000 !important;
  background-color: #FFFFFF !important;
}

.ReactTable .rt-tbody .rt-tr-group:last-child {
  border-bottom: solid 1px rgba(0,0,0,0.05) !important;
}

.ReactTable .rt-thead .rt-th.-sort-asc, 
.ReactTable .rt-thead .rt-td.-sort-asc {
  box-shadow: inset 0 3px 0 0 red;
}

.ReactTable .rt-thead .rt-th.-sort-desc, 
.ReactTable .rt-thead .rt-td.-sort-desc {
  box-shadow: inset 0 -3px 0 0 red;
}

.grid-layout {
  position: relative;
  background-color: #FFFFFF;
}

.grid-box {
  position: absolute;
  border: 2px solid #4b4f56;
  overflow-x: hidden;
  overflow-y: hidden;
  background-color: #FFFFFF;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column;
          flex-flow: column;
  height: 100%;
}

.grid-box-title {
  padding: 3px;
  -webkit-flex: 0 1 auto;
          flex: 0 1 auto;
}

.grid-box-content {
  -webkit-flex: 1 1 auto;
          flex: 1 1 auto;
  overflow-y: auto;
}

.grid-draggable {
  height: 18px;
  width: 18px;
  font-size: 16px;
  cursor: -webkit-grab;
  cursor: grab;
  position:absolute; 
  right: 3px; 
  top: 3px; 
}

.grid-resizable {
  width: 12px;
  height: 12px;
  position:absolute; 
  right: 0; 
  bottom: 0; 
  cursor: move; 
}

.grid-box-file-button-group {
  float: right;
  display: none;
}

.grid-box-title:hover .grid-box-file-button-group {
  float: right;
  display: block;
  transition: 0.3s;
}

.tab-header {
